body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  background-color: #ffffff;
  min-width: 320px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-v14-latin-regular.woff2") format("woff2"), url("../fonts/montserrat-v14-latin-regular.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v14-latin-500.woff2") format("woff2"), url("../fonts/montserrat-v14-latin-500.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/montserrat-v14-latin-900.woff2") format("woff2"), url("../fonts/montserrat-v14-latin-900.woff") format("woff"); }

@font-face {
  font-family: "Pacifico";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pacifico-v16-latin-regular.woff2") format("woff2"), url("../fonts/pacifico-v16-latin-regular.woff") format("woff"); }

.nav {
  margin: 10px 0 10px 10px; }

.nav__list {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  position: absolute;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  line-height: 22px;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #ffffff; }

.nav--closed .nav__toggle {
  position: relative;
  background-color: #802d25;
  border: 0;
  width: 25px;
  height: 25px; }
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/menu-button.svg);
    width: 25px;
    height: 25px;
    top: 0;
    left: 0; }
  @media (min-width: 768px) {
    .nav--closed .nav__toggle {
      display: none; } }

.nav--opened .nav__toggle {
  position: relative;
  background-color: #802d25;
  border: 0;
  width: 25px;
  height: 25px; }
  .nav--opened .nav__toggle::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/menu-button.svg);
    width: 25px;
    height: 25px;
    top: 0;
    left: 0; }
  @media (min-width: 768px) {
    .nav--opened .nav__toggle {
      display: none; } }

@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none; } }

.header {
  display: flex;
  background-color: #802D25; }

.logo-title {
  font-family: "Pacifico", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  margin: 0;
  padding: 0;
  color: #ffffff;
  cursor: pointer; }

.logo-title__link {
  align-self: center;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0); }
